<template>
    <div class="recommend full-width">
        <div class="container">
            <img src="../assets/recommend.png" alt="">
            <div class="recommend-item" v-for="(i,k) in recommend" v-if="k < 4">
                <a :href="`/goods.html#/${i.product_id}`" target="_blank">
                    <img :src="i.default_img|img" alt="">
                    <div class="recommend-info">
                        <p class="rec-tile">{{i.product_name}}</p>
                        <p class="rec-price">{{i |exch(ex)}}{{ex.unit}}</p>
                        <a @click.stop.prevent="addCart(i.product_id)" class="rec-buy">
                            
                        </a>
                    </div>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "recommond",
        data() {
            return {
                recommend: Array,
                ex: Object
            }
        },
        mounted() {
            this.guessYouLike()
        },
        methods: {
            guessYouLike() {
                this.yum('api/ai/guessYouLike').then(
                    res => {
                        this.recommend = res.data.data;
                        this.ex = {
                            exchange: res.data.exchange,
                            has_custom_price: res.data.has_custom_price,
                            unit: res.data.unit
                        }
                    }
                )
            },
            addCart(id){
                let data ={
                    product_id:id,
                    status:1,
                    nums:1
                }
                this.yum('api/member/addCart',data).then(res=>{
                    if (res.data.status === 200){
                        this.bus.$emit('topNav')
                        this.bus.$emit('cart_notice');
                    }
                })
            }
        }
    }
</script>

<style scoped lang="stylus">
    div.recommend
        height 160px
        margin-top 20px
        .container
            background-color #f8f8f8
            height 160px
            .recommend-item
                width 260px
                height 120px
                margin 20px 10px
                float: right
                background-color white
                img
                    margin 10px
                    width 100px
                    height 100px
                .recommend-info
                    float right
                    width 140px
                    &:hover .rec-tile
                        color #ff0077
                    .rec-tile
                        padding 10px
                        height 52px
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        overflow: hidden;
                        font-size 12px
                    .rec-price
                        padding 0 10px
                        margin-bottom 13px
                        font-size 12px
                        color #ff0077
                        font-weight bold
                    .rec-buy
                        color black
                        background-color #f8f8fa
                        border 1px solid #dcdcdc
                        padding 8px 10px
                        border-radius 4px
                        margin-left 10px
                        &:hover
                            color white
                            background-color #ff0077
                            border 1px solid #f07
</style>