<template>
    <div class="filters full-width">
        <div class="container">
            <div class="classify">
                <ul>
                    <li><a href="/grade.html"><span></span></a><img class="right-point" src="../assets/right-black.png" alt=""></li>
                    <li>
                        <div class="classify-filter"><span>{{father.name}}</span>
                            <div class="hack"></div>
                            <i class="icon"></i>
                            <div class="classify-filter-all">
                                <ul>
                                    <li><a @click="postData({page: 1, cid: null, max_price: null, min_price: null, brand_name: null})"></a></li>
                                    <li v-for="(i,k) in classify.child"><a @click="ichi(i,k)">{{i.cname}}</a></li>
                                </ul>
                            </div>
                        </div>
                        <img class="right-point" src="../assets/right-black.png">
                    </li>
                    <li v-if="father.id!==0">
                        <div class="classify-filter"><span>{{child.name}}</span>
                            <div class="hack"></div>
                            <i class="icon"></i>
                            <div class="classify-filter-all">
                                <ul>
                                    <li><a @click="$router.go(-1)"></a></li>
                                    <li v-for="(ii,kk) in classify.child[father.index].child"><a @click="ni(ii,kk)">{{ii.cname}}</a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <img class="right-point" src="../assets/right-black.png">
                    </li>
                    <li v-if="child.id!==0&&classify.child[father.index].child[child.index].child.length>0">
                        <div class="classify-filter"><span>{{GrandChild.name}}</span>
                            <div class="hack"></div>
                            <i class="icon"></i>
                            <div class="classify-filter-all">
                                <ul>
                                    <li><a @click="$router.go(-1)"></a></li>
                                    <li v-for="(iii,kkk) in classify.child[father.index].child[child.index].child"><a
                                            @click="sann(iii,kkk)">{{iii.cname}}</a></li>
                                </ul>
                            </div>
                        </div>
                        <img class="right-point" src="../assets/right-black.png">
                    </li>
                </ul>
            </div>
            <div class="brand filter" :class="{switchOn:swatch}">
                <div class="filter-tile"></div>
                <ul class="filter-items">
                    <li class="filter-items">
                        <a @click="postData({brand_name:null,page:1})"
                           :class="{choosed:$route.query.brand_name===null}"></a>
                    </li>
                    <li class="filter-items" v-for="(i,k) in brand" v-if="k<15">
                        <a @click="postData({brand_name:i.brand_name,page:1})"
                           :class="{choosed:i.brand_name===$route.query.brand_name}">{{ i.brand_name }}</a>
                    </li>
                </ul>
                <div class="filter-more">
                    <span v-if="!swatch" @click="swatch = !swatch"><img src="../assets/down-blac.png" alt=""></span>
                    <span class="up" v-else @click="swatch = !swatch"><img src="../assets/up-red.png" alt=""></span>
                </div>
            </div>
            <div class="price-filter filter">
                <div class="filter-tile"></div>
                <ul class="filter-items">
                    <li class="filter-items">
                        <a @click="postData({min_price:null,max_price:null,page:1})"
                           :class="{choosed:$route.query.min_price===null}"></a>
                    </li>
                    <li class="filter-items" v-for="i in 7">
                        <a @click="postData({min_price:i*1000-1000,max_price:i*1000-1,page:1})"
                           :class="{choosed:i*1000-1000==$route.query.min_price}"
                        >{{`${(i*1000-1000)/parseInt(ex.exchange)}-${(i*1000-1)/parseInt(ex.exchange)}`}}</a>
                    </li>
                    <li class="filter-items">
                        <a @click="postData({min_price:10000,max_price:9999999,page:1})"
                           :class="{choosed:10000==$route.query.min_price}"
                        >10000</a>
                    </li>
                </ul>
            </div>
            <div class="sorts-filter">
                <ul class="sorts">
                    <li class="sort" :class="{active:filter.type == 3}" @click="sorts({type:3,page:1})"><span></span>
                    </li>
                    <li class="sort" :class="{active:filter.type == 2}" @click="sorts({type:2,page:1})"><span><i
                            class="asc-ico"></i></span>
                    </li>
                    <li class="sort" :class="{active:filter.type == 1}" @click="sorts({type:1,page:1})"><span><i
                            class="asc-ico"></i></span>
                    </li>
                    <li class="sort" :class="{active:filter.can == 1}" @click="can()"><span></span>
                    </li>
                </ul>
                <div class="price-filter" :class="{onFocus:onfocus}" @mouseleave="onfocus = false">
                    <input @focus="onfocus = true" type="text" placeholder="" v-model="minPrice">
                    -
                    <input @focus="onfocus = true" type="text" placeholder="" v-model="maxPrice">
                    <div v-if="onfocus">
                        <a class="button cursor" @click="priceFilter()"></a>
                        <a class="cursor" @click="onfocus = false"></a>
                    </div>
                </div>
                <div class="sorts-page" v-if="PageToTal>0">
                    <span class="active">{{filter.page}}</span><span>/{{PageToTal}}</span>
                    <button class="cursor" :disabled="filter.page == 1"><img src="../assets/v.png" alt=""
                                                                              @click="postData({page:parseInt(filter.page)-1})">
                    </button>
                    <button class="cursor" :disabled="filter.page == PageToTal"><img src="../assets/r.png" alt=""
                                                                                      @click="postData({page:parseInt(filter.page)+1})">
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "filters",
        data() {
            return {
                swatch: false,
                father: {id: 0, name: '', index: 0},
                child: {id: 0, name: '', index: 0},
                GrandChild: {id: 0, name: '', index: 0},
                minPrice: '',
                maxPrice: '',
                onfocus: false
            }
        },
        created() {
            window.onpopstate = function () {
                if (this.$route.query.page) {
                    this.$store.dispatch('state/postData', this.$route.query);
                } else {
                    this.$store.dispatch('state/postData', {page: 1});
                }
            }.bind(this);
            this.$store.dispatch('filters/CategoryLists');
            if (this.$route.query.page) {
                this.$store.dispatch('state/postData', this.$route.query);
            } else {
                this.$store.dispatch('state/postData', {page: 1});
            }
        },
        computed: {
            ...mapState('state', ['filter', 'ex', 'brand', 'PageToTal']),
            ...mapState('filters', {
                classify: state => state.classify
            })
        },
        methods: {
            priceFilter() {
                if (parseInt(this.maxPrice) > parseInt(this.minPrice)) {
                    this.postData({min_price: this.minPrice, max_price: this.maxPrice, page: 1})
                }
                this.onfocus = false
            },
            sorts(data) {
                if (data.type != this.filter.type) {
                    data = Object.assign({order: 'asc'}, data);
                    this.postData(data)
                } else {
                    if (this.filter.order === 'asc') {
                        data = Object.assign({order: 'desc'}, data);
                        this.postData(data)
                    } else {
                        data = Object.assign({order: 'asc'}, data);
                        this.postData(data)
                    }
                }
            },
            can(){
              if (this.filter.can == 1){
                  this.postData({can:''})
              }else{
                  this.postData({can:1})
              }
            },
            postData(data) {
                data = Object.assign(data, {keyword: ''});
                this.$store.dispatch('state/postData', data);
                this.$router.push({
                    query: this.filter
                });
            },
            ichi(i, k) {
                this.father.id = i.cid;
                this.father.name = i.cname;
                this.father.index = k;
                this.postData({page: 1, cid: i.cid, max_price: null, min_price: null, brand_name: null})
            },
            async ni(i, k) {
                this.child.id = i.cid;
                this.child.name = i.cname;
                this.child.index = k;
                await this.postData({page: 1, cid: i.cid, max_price: null, min_price: null, brand_name: null});
                this.$store.dispatch('state/postBrand')
            },
            sann(i, k) {
                this.GrandChild.id = i.cid;
                this.GrandChild.name = i.cname;
                this.GrandChild.index = k;
                this.postData({page: 1, cid: i.cid, max_price: null, min_price: null, brand_name: null})
            }
        }
    }
</script>

<style scoped lang="stylus">
    .classify
        height 60px
        line-height 60px
        border-bottom 1px solid #ccc
        li
            display inline-block
            &:last-child>img.right-point
                display none
        img.right-point
            margin 0 10px
            transform translateY(-1px)
        div.classify-filter
            display inline-block
            position relative
            height 26px
            vertical-align middle
            line-height 26px
            padding 0 0 0 10px
            cursor pointer
            li
                width 80px
                height 28px
                margin-right 5px
                a
                    display inline-block
                    overflow hidden
                    width inherit
                    height 28px
                    text-overflow ellipsis
                    white-space nowrap
            .hack
                border: solid 1px #ccc;
                display block
                width 100%
                height 100%
                position absolute
                top 0
                left 0
                z-index 0
            .icon
                display inline-block
                width 11px
                height 6px
                vertical-align middle
                background-image url("../assets/down-blac.png")
                margin-right 5px
                margin-left 5px
                transition 0.3s
            &-all
                padding 10px 10px 15px
                width 370px
                position absolute
                border solid 1px #ff0077
                background-color white
                display none
                top 25px
                left 0
            &:hover
                color #ff0077
                .hack
                    border: solid 1px #f07;
                    border-bottom 1px solid white
                    z-index 3
                .classify-filter-all
                    display block
                    z-index 2
                    transition 0.3s
                    background-color white
                .icon
                    background-image url("../assets/up-red.png")
                    transition 0.3s

    .filter
        position relative
        height 50px
        line-height 50px
        border-bottom 1px solid #ccc
        overflow hidden
        &-tile
            width 70px
            background-color #f8f8fa
            line-height 49px
            text-align center
            color black
            font-weight bold
            font-size 12px
            vertical-align middle
            float left
        &-items
            display inline-block
            max-width 1000px
            li
                display inline-block
                width 100px
                text-align center
                font-size 12px
        &-more
            float right
            width 60px
            margin-right 10px
            span
                display inline-block
                width 60px
                height 26px
                line-height 26px
                font-size 12px
                vertical-align middle
                text-align center
                border 1px solid #ccc
                cursor pointer
                user-select none
                img
                    margin-left 5px
                    transform translateY(-2px)
                &.up
                    border-color #ff0077
                    color #ff0077
        &.switchOn
            height auto

    .sorts-filter
        height 40px
        line-height 40px
        background-color #e8e7e7
        margin-top 10px
        .sorts
            display inline-block
            float left
            .sort
                display inline-block
                width 70px
                text-align center
                color black
                cursor pointer
                user-select none
                span
                    font-size 12px
                    font-weight bold
                &:hover
                    color #ff0077
                    .asc-ico
                        background-image url("../assets/price-over.png")
                &.active
                    color white
                    background-color #ff0077
                    .asc-ico
                        background-image url("../assets/price-on.png")
                .asc-ico
                    width 7px
                    height 11px
                    display inline-block
                    margin-left 10px
                    background-image url("../assets/price-default.png")
        .price-filter
            display inline-block
            float left
            margin-left 20px
            padding-left 10px
            padding-right 10px
            border 1px solid #e8e7e7
            line-height 38px
            position absolute
            z-index 1
            &.onFocus
                background-color white
                border 1px solid #dcdcdc
                box-sizing border-box
            input[type=text]
                width 70px
                height 26px
                padding-left 5px
                outline none
                display inline-block
                &:focus
                    border 1px solid red
            a.button
                width 48px
                height 26px
                line-height 26px
                display inline-block
                border-radius: 2px;
                text-align center
                border: solid 1px #dcdcdc;
                margin-right 36px
        .sorts-page
            display inline-block
            float right
            margin-right 20px
            img
                margin-left 10px

    span.active
        color #ff0077

    a.choosed
        color #ff0077

    .disabled
        cursor pause

    button.cursor
        border none
        outline none
        background inherit
</style>