/**
 * @file state module 
 *
 * @author ryu
 */

export const FILTERS = 'FILTERS';
export const GRADESTATE = 'GRADESTATE';
export const EXCHANGE = 'EXCHANGE';
export const BRAND = 'BRAND';
export const LOAD = 'LOAD';
import yum from '../../../components/yum'

export const state = () => {
    return {
        /**
         * @type {boolean}
         */
        GradeState: [],
        filter: {
            limit: 20,
            order: 'asc',
            type: 3,
        },
        ex: {
            exchange: 1
        },
        PageToTal: '',
        brand: [],
        loading: false
    };
};

export const mutations = {

    [FILTERS](state, filters) {
        state.filter = Object.assign(state.filter, filters)
    },
    [GRADESTATE](state, GradeState) {
        state.GradeState = Object.assign([], GradeState.list);
        state.PageToTal = Math.ceil(parseInt(GradeState.total) / parseInt(state.filter.limit)) || 0
    },
    [EXCHANGE](state, ex) {
        state.ex = Object.assign({}, ex)
    },
    [BRAND](state, brand) {
        state.brand = Object.assign([], brand)
    },
    [LOAD](state, swit) {
        state.loading = swit
    }
};

export const actions = {
    /**
     * @param {Function} commit commit
     * @param {Object}  registerData
     * 
     */
    //

    commitFilters({commit}, data) {
        commit(FILTERS, data);
    },
    postData({state, commit}, data) {
        commit(FILTERS, data);
        commit(GRADESTATE, []);
        commit(LOAD, true);
        yum('api/Product/getGoodsList', Object.assign(state.filter, data)).then(
            res => {
                if (res.data.status === 200) {
                    commit(GRADESTATE, res.data.data);
                    let ex = {
                        exchange: res.data.exchange,
                        has_custom_price: res.data.has_custom_price,
                        unit: res.data.unit
                    };
                    commit(EXCHANGE, ex);
                    commit(LOAD, false);
                } else {
                    commit(LOAD, false);
                }
            }
        )
    },
    postBrand({state, commit}) {
        let data = {cid: state.filter.cid};
        yum('api/Brand/getBrandList', data).then(res => {
            if (res.data.status === 200) {
                commit(BRAND, res.data.data)
            }
        })
    }
};
