<template>
    <div class="page full-width" v-if="PageToTal>0">
        <div class="container ac">
            <ul>
                <li class="front-page "><label class="cursor"><img style="margin-right: 10px" src="../assets/v.png"
                                                                   alt="">
                    <button @click="postData({page:parseInt(query.page)-1})" :disabled="query.page==1"></button>
                </label></li>
                <li class="page-item cursor" v-show="query.page>1" @click="postData({page:1})">1</li>
                <li class="page-item" v-show="query.page>3">  </li>
                <li class="page-item cursor" v-show="query.page>2" @click="postData({page:parseInt(query.page)-1})">
                    {{parseInt(query.page)-1}}
                </li>
                <li class="page-item cursor thispage" @click="postData({page:parseInt(query.page)})">
                    {{parseInt(query.page)}}
                </li>
                <li class="page-item cursor" v-show="query.page<PageToTal-1"
                    @click="postData({page:parseInt(query.page)+1})">{{parseInt(query.page)+1}}
                </li>
                <li class="page-item" v-show="query.page<PageToTal-2">  </li>
                <li class="page-item cursor" v-show="filter.page<PageToTal" @click="postData({page:PageToTal})">
                    {{PageToTal}}
                </li>
                <li class="behind-page "><label class="cursor">
                    <button @click="postData({page:parseInt(query.page)+1})" :disabled="query.page==PageToTal"></button>
                    <img style="margin-left: 10px" src="../assets/r.png" alt=""></label></li>
            </ul>
            <div class="jump-page">
                {{PageToTal}}
                <input class="ac" type="text" v-model="insetPage">
                
                <button class="cursor" @click="postData({page:insetPage})" :disabled="insetPage>PageToTal||insetPage<1">
                    
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "page",
        data() {
            return {
                insetPage: ''
            }
        },
        methods: {
            postData(data) {
                this.$store.dispatch('state/postData', data);
                this.$router.push({
                    query: this.filter
                });
                document.documentElement.scrollTop = 222
                // this.$router.go(0)
            },
        },
        mounted() {
            let data = Object.assign(
                this.filter,
                this.$route.query
            )
            this.$router.push({query:data});
        },
        computed: {
            ...mapState('state', ['filter', 'PageToTal']),
            query: {
                get() {
                    return this.$route.query
                },
                set() {
                    this.$route.query = Object.assign({}, this.filter)
                }
            }
        }
    }
</script>

<style scoped lang="stylus">
    .page
        margin-top 100px
        margin-bottom 100px
        ul
            height 40px
            line-height 40px
            display inline-block
            li
                margin-right 10px
                display inline-block
            .page-item
                width 40px
                height 40px
                &.cursor
                    border-radius 2px
                    border 1px solid #ccc
                    &.thispage
                        background-color #ff0077
                        border #ff0077
                        color white
            .front-page, .behind-page
                width 100px
                border 1px solid #ccc
                border-radius 2px
                line-height 40px
                height 40px
                label
                    display block
                    button
                        display none
        .jump-page
            display inline-block
            margin-left 30px
            input
                width 40px
                height 40px
                outline none
                border 1px solid #ccc
                border-radius 2px
            button
                width 50px
                height 40px
                outline none
                border 1px solid #ccc
                border-radius 2px
                background-color white
</style>