export const CLASSIFY = 'CLASSIFY';
export const BRAND = 'BRAND';
import yum from '../../../components/yum'

export const state = () => {
    return {
        /**
         * @type {boolean}
         */
        classify: {},
        brand: {}
    };
};

export const mutations = {
    [CLASSIFY](state, res) {
        state.classify = Object.assign({}, res)
    },
    [BRAND](state, brand) {
        state.brand = Object.assign([], brand)
    }
};

export const actions = {
    CategoryLists({commit}) {
        yum('api/category/lists').then(
            res => {
                if (res.data.status === 200) {
                    commit(CLASSIFY, res.data.data[0])
                }
            }
        )
    }
};
